ement zur Verfügung. ")]),_c('h4',{attrs:{"id":"anschreiben-schlussteil-letzter-satz"}},[_vm._v("Letzter Satz")]),_c('p',[_vm._v(" Im letzten Satz des Anschreibens verabschiedest Du Dich höflich und bekräftigst, dass Du Dich über eine Einladung zum Vorstellungsgespräch freust. ")]),_c('blockquote',{staticClass:"lead"},[_vm._v(" Über eine Einladung zum persönlichen Gespräch freue ich mich. ")]),_vm._m(4),_c('p',[_vm._v(" Achte auch hier auf eine übersichtliche Darstellung und passe das Design farblich dem Anschreiben an. ")]),_c('h3',{attrs:{"id":"lebenslauf-aufbau"}},[_vm._v("Aufbau und Inhalte")]),_c('p',[_vm._v(" Was gehört alles in einen Lebenslauf? Hier geben wir Dir eine Übersicht. ")]),_c('b-img',{staticClass:"p-2",staticStyle:{"width":"50vw","max-width":"350px"},attrs:{"right":"","src":require("@/assets/img/personal_data__monochromatic.svg"),"alt":"Grafik von einem Lebenslauf"}}),_c('h4',{attrs:{"id":"lebenslauf-aufbau-persoenliche-daten"}},[_vm._v("Persönliche Daten")]),_c('p',[_vm._v(" Zu Deinen perönlichen Daten gehören "),_c('strong',[_vm._v("Vor- und Nachname, Anschrift")]),_vm._v(". Heutzutage haben Dinge wie Staatsangehörigkeit, Familienstand oder Geburtsdatum im Normalfall nicht in die Bewerbung. Das verhindert zumindest in gewissem Maße, dass Du wegen dieser Faktoren diskriminierst wirst. Sollte Dir so etwas im Laufe Deiner Bewerbung passieren, solltest Du Dich unbedingt an "),_c('b-link',{attrs:{"href":"https://www.antidiskriminierungsstelle.de/DE/startseite/startseite-node.html","target":"_blank"}},[_vm._v("entsprechende Stellen")]),_vm._v(" wenden. ")],1),_c('h4',{attrs:{"id":"lebenslauf-aufbau-berufliche-stationen"}},[_vm._v("Berufliche Stationen")]),_c('p',[_vm._v(" Liste deine beruflichen Stationen auf und schreibe immer 2-3 Beispiele zu den Aufgaben, die du bei den einzelnen Arbeitgebern hattest. Das macht deinen Lebenslauf etwas lebendiger und hebt dich von anderen Bewerbern ab. Auch Praktika und Ausbildung gehören hierher. Achte darauf, dass